import React, { Component } from 'react';
import axios from 'axios';
import Moives from './Movies';
import Loading from './Loading';
import './Section.scss';

// 실행순서 : render() -> componentDidMount()는 render 함수 후에 작용
// -> setState():바뀐 내용이 있으면 -> render(): 자동으로 수정
// 즉 render()의 콜백함수 같은 개념이라고 생각하면 됨
class Section extends Component {
    state = {
        kim : [],
        isLoading : true
    }

    // async 비동기 방식
    getMovies = async () => {
        const { data : { data : { movies } } } = await axios.get("https://yts.mx/api/v2/list_movies.json")
        this.setState({
            kim : movies /* 위의 state 안에 있는 movies : json으로 갖고오는 data:movies */
        })
    }

    // render() 수행 후 호출되는 함수 componentDidMound()
    componentDidMount() {
        // setTimeout(함수, 5000);
        setTimeout(()=>{
            this.setState({
                isLoading : false
            })
        }, 5000);
        this.getMovies();
    }

    render() {
        const { kim, isLoading } = this.state;
        return (
            <>
                {
                    isLoading ? <Loading /> : (
                        <section className="container"> {
                            kim.map( (movies)=>(
                                <Moives key={movies.id} year={movies.year} title={movies.title} summary={movies.summary} poster={movies.medium_cover_image} genres={movies.genres} />
                                ))
                            }
                        </section>)
                }
            
            </>




            // <div>
            //     <section className="container">
            //         {
            //             kim.map( (movies)=>(
            //                 /* key값은 무조건 넣어야함(유일한 값) 
            //                 movies.id의 movies는 map() 안의 매개변수 movies 임 */
            //                 <Moives key={movies.id} year={movies.year} title={movies.title} summary={movies.summary} poster={movies.medium_cover_image} genres={movies.genres} />
            //             ))
            //         }
            //     </section>
            // </div>
        );
    }
}

export default Section;